.App {
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
}

input {
    margin-left: 25px;
}

.game {
    position: relative;
    width: 660px;
    height: 359px;
}

.node {
    position: absolute;
    width: 50px;
    height: 50px;
    border-radius: 100%;
    background-color: lightgray;
}

.node.hound {
    background-color: red;
}

.node.hare {
    background-color: aqua;
}

.node.playable {
    cursor: pointer;
}

.node.highlighted {
    width: 45px;
    height: 45px;
    border: 5px solid darkgray;
}